.spinner {
  width: 22px;
  height: 22px;
  border-radius: 50%;
  border: 3px solid red;
  border-right: 3px solid transparent;
  animation: circleloader 0.7s infinite ease-in-out;
}
@keyframes circleloader {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
